import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import './Datas.css'
const myList = ["SAT:GSAT-17", "TRANSPONDER:C-09", 'UPLINK FREQ:6256.5MHz', 'S/R:6.25MSPS', 'POL:VERTICAL']; 
const satList=['Name','lookangle','Frequency','Service','Beacon Frq'];
const bcolor=['blue','black','green','magenta','black'];
const satlistItems = satList.map((list,i)=>{   
  return <Col style={{backgroundColor: bcolor[i]}}>{list}</Col>;   
}); 
const listItems = myList.map((list)=>{   
    return <p>{list}</p>;   
});   
export default function Datas(){
    return (
        <div className='Datas' style={{ backgroundColor: "black" ,fontSize:16}}>
            <Tabs className="Tabs">
              <TabList>
                <Tab>UPLINK</Tab>
                <Tab>SATELLITES</Tab>
                <Tab>IRD</Tab>
                <Tab>MODULATOR</Tab>
              </TabList>
              <TabPanel>
                <ul style={{fontSize:16}}> {listItems} </ul>
              </TabPanel>
              <TabPanel>
                <div style={{ display: 'block', width: 600, padding: 30 ,fontSize:16}}>
                  <Container> 
                    <Row>
                      {satlistItems}
                    </Row>
                  </Container>
                </div>
              </TabPanel>
              <TabPanel>
                <p>Tab 3 works!</p>
              </TabPanel>
              <TabPanel>
                <p>Tab 4 works!</p>
              </TabPanel>
            </Tabs>
        </div>
      );
}