import React from "react";
import { useEffect, useRef } from 'react';
import './Drawing.css';
export default function Downlink() {
var sr=new Array(11);
var dst=new Array(11);
var draw=false;
var nIntervId;
var did=11;
var sid=11;
var x1=0;
var xsrc=0;
var ysrc=0;
var xmid=0;
var xdst=0;
var ydst=0;
var down=true;
var ind=0;
var ddp=0;
for (var i = 0; i < sr.length; i++) {
    sr[i] = new Array(3);
    dst[i]=new Array(3);
};
for ( i = 0; i < 11; i++) {
  for (var j = 0; j < 3; j++) {
      sr[i][j] = 11;
      dst[i][j]=11;
  }
};
const canvasRef = useRef(null);
const ctxRef = useRef(null);
const xb=30;
var value=true;
var ckt=false;
const sources1 = ['ASI', 'ASI', 'ENC',' ENC',' ENC','ENC',' MOD',' MOD',' MOD',' MOD'];
const sources2 = [' IRD-1', 'IRD-2', '1-M','1-R','2-M','2-R','1-M','1-R','2-M','2-R'];                
const sources3 = ['OUT', 'OUT', 'MON','MON','MON','MON','MON','MON','MON','OUT-2'];
const destinations1=['ASI','ASI','9"','9"','9"','9"','A/V','17"','ENC2'];
const destinations2=['IRD-1','IRD-2','MON','MON','MON','MON','MON','MAIN','REDUN'];
const destinations3=['  IN','  IN','1','2','3','4','','','SD-IN'];
const equipments=['ASI-IRD-1','ASI-IRD-2','ENC-1M','ENC-1R','ENC-2M','ENC-2R','MOD-1M','MOD-1R','MOD-2M','MOD-2R'];
useEffect(() => {
  const canvas = canvasRef.current;
  // For supporting computers with higher screen densities, we double the screen density
  canvas.width = window.innerWidth * 2;
  canvas.height = window.innerHeight * 2;
  canvas.style.width = `${window.innerWidth}px`;
  canvas.style.height = `${window.innerHeight}px`;
  const ctx = canvas.getContext('2d');
  ctx.scale(2, 2);
  ctx.lineCap = 'round';
  ctx.strokeStyle = 'yellow';
  ctx.lineWidth = 2;
  ctxRef.current = ctx;
  ctxRef.current.beginPath();
  ctxRef.current.moveTo(0,100);
  srcbutton();
  dstbutton();
  equbutton();
  takebutton();
  prevbutton();
  //menubutton();
  ctxRef.current.shadowBlur = 0;
},[]);
function menubutton(){
  neonRect(xb-10,10,20,20,13,213,252);//cyan -TAKE BUTTON
  writeText({text:"M",x:xb-10+5,y:15});
}
function arrow(xa,ya,direction){
  if(direction===1){
  ctxRef.current.moveTo(xa,ya-3);
  ctxRef.current.lineTo(xa-7,ya);
  ctxRef.current.lineTo(xa,ya+3);
  ctxRef.current.stroke();
  }
  else{
    ctxRef.current.moveTo(xa,ya);
  ctxRef.current.lineTo(xa-7,ya-3);
  ctxRef.current.moveTo(xa-7,ya+3);
  ctxRef.current.lineTo(xa,ya);
  ctxRef.current.stroke();
  }
}  
const drawRectangle =function(x,y,w,h,border){
  const ctx=ctxRef.current;
  ctx.beginPath();
  ctx.moveTo(x+border, y);
  ctx.lineTo(x+w-border, y);
  ctx.quadraticCurveTo(x+w-border, y, x+w, y+border);
  ctx.lineTo(x+w, y+h-border);
  ctx.quadraticCurveTo(x+w, y+h-border, x+w-border, y+h);
  ctx.lineTo(x+border, y+h);
  ctx.quadraticCurveTo(x+border, y+h, x, y+h-border);
  ctx.lineTo(x, y+border);
  ctx.quadraticCurveTo(x, y+border, x+border, y);
  ctx.closePath();
  ctx.stroke();
};
var neonRect = function(x,y,w,h,r,g,b){
  const ctx=ctxRef.current;
  ctx.shadowColor = "rgb("+r+","+g+","+b+")";
  ctx.shadowBlur = 10;
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=7.5;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=6;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=4.5;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=3;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= '#fff';
  ctx.lineWidth=1.5;
  drawRectangle(x,y,w,h,1.5);
}
const clickEffect=({nativeEvent})=>{
  
  const {offsetX,offsetY}=nativeEvent;
  for (const [i, sx] of sr.entries()) {
    const xc=sx[0];
    const yc=sx[1];
    //SOURCE KEY
    if(offsetX>xc&&offsetX<xc+40){
      if(offsetY>yc&&offsetY<yc+40){
        srcbutton();
        neonRect(xc,yc,40,40,252,90,184);
        if(did===11){
          dstbutton();
          for(const [j,vx] of dst.entries()){
            if(vx[2]===i){
              neonRect(vx[0],vx[1],40,40,252,90,184);
            }
          }
        }
        else{
          nIntervId = setInterval(blinker, 500);
          sid=i;
        }
        equbutton();
        return;
      }
    }
  }
  //DESTINATION KEY
  for (const [i, sx] of dst.entries()) {
    const xc=sx[0];
    const yc=sx[1];
    if(offsetX>xc&&offsetX<xc+40){
      if(offsetY>yc&&offsetY<yc+40){
        dstbutton();
        srcbutton();
        equbutton();
        neonRect(xc,yc,40,40,252,90,184);
        did=i;
      return;
    }
    }
  }
  //TAKE KEY
  if(offsetX>xb+300&&offsetX<xb+340){
    if(offsetY>25&&offsetY<65){
      if(draw)
        return;
      draw=true;
      dst[did][2]=sid;
      clearInterval(nIntervId);
      value=true;
      takebutton();
      drawLink();
      nIntervId=setInterval(dl,10);
      return;
    }
  }
  //PREV button
  if(offsetX>xb+300&&offsetX<xb+340){
    if(offsetY>85&&offsetY<125){
      if(draw)
        return;
      srcbutton();
      dstbutton();
      equbutton();
      draw=true;
      value=true;
      ckt=false;
      ctxRef.current.lineCap = 'round';
      ctxRef.current.strokeStyle = 'yellow';
      ctxRef.current.lineWidth = 1;
      nIntervId=setInterval(full,8);
      return;
    }
  }
  //Menu
  if(offsetX>xb-10&&offsetX<xb+10){
    if(offsetY>10&&offsetY<30){
      ctxRef.current.clearRect(xb-10,30,300,600);
    }
  }
}

function drawLink(){
  xsrc=sr[sid][0]+45;
  ysrc=sr[sid][1]+20;
  xdst=dst[did][0]-5;
  ydst=dst[did][1]+20;
  xmid=(xdst-xsrc)/2;
  x1=0;
  if(ydst>ysrc)
    down=true;
  else
    down=false;
  ckt=true;
  ctxRef.current.moveTo(xsrc,ysrc);
}
function dl(){
  if(!ckt)
    return;
  x1=x1+1;
  if(x1>=xmid ){
    if(down){
      if(ysrc<ydst)
        ysrc=ysrc+1;
      else
        xsrc=xsrc+1;
    }
    else{
      if(ysrc>ydst)
        ysrc=ysrc-1;
      else
        xsrc=xsrc+1;
    }
  }
  else{
    xsrc=xsrc+1;
  }
  ctxRef.current.lineTo(xsrc,ysrc);
  ctxRef.current.stroke();
  if(xsrc>=xdst){
    clearInterval(nIntervId);
    did=11;
    sid=11;
    draw=false;
    ckt=false;
  }
}
function full(){
  if(!ckt){
  if(dst[ind][2]!==11){
    sid=dst[ind][2];
    did=ind;
    drawLink();
    ckt=true;
  }
}
  x1=x1+1;
  if(x1>=xmid ){
    if(down){
      if(ysrc<ydst)
        ysrc=ysrc+1;
      else
        xsrc=xsrc+1;
    }
    else{
      if(ysrc>ydst)
        ysrc=ysrc-1;
      else
        xsrc=xsrc+1;
    }
  }
  else{
    xsrc=xsrc+1;
  }
  ctxRef.current.lineTo(xsrc-4+ddp,ysrc);
  ctxRef.current.stroke();
  if(xsrc>=xdst){
    did=11;
    sid=11;
    x1=0;
    ddp=ddp+1
    ckt=false;
    if(ind<9)
      ind=ind+1;
      else{
        ind=0;
        ddp=0;
        draw=false;
      clearInterval(nIntervId)
      }
  }
}
function blinker()  {
      value=!value;
      takebutton();
};
const equbutton=()=>{
  ctxRef.current.clearRect(xb-10,0,90,600);
  for (const [i, equipment] of equipments.entries()) {
    neonRect(xb,25+i*60,60,40,13,213,252);//cyan
    ctxRef.current.fillStyle='yellow';
    ctxRef.current.moveTo(xb+60,45+i*60);
    ctxRef.current.lineTo(xb+100,45+i*60);
    arrow(xb+80,45+i*60,0);
    ctxRef.current.stroke();
    writeText({text:equipment,x:xb+5,y:35+i*60});
  }  
}  

const srcbutton=()=>{
  ctxRef.current.clearRect(xb+80,0,100,650);
  for (const [i, source] of sources1.entries()) {
    neonRect(xb+100,25+i*60,40,40,13,213,252);//cyan
    sr[i][0]=xb+100;
    sr[i][1]=25+i*60;
    ctxRef.current.fillStyle='yellow';
    writeText({text:sources1[i],x:xb+105,y:30+i*60});
    writeText({text:sources2[i],x:xb+105,y:40+i*60});
    writeText({text:sources3[i],x:xb+105,y:50+i*60});
  }
}
const dstbutton=()=>{
  const yy=30;
  ctxRef.current.clearRect(xb+160,yy-10,100,600);
  for (const [i, destination] of destinations1.entries()) {
    neonRect(xb+200,yy+i*60,40,40,13,213,252);//cyan
    dst[i][0]=xb+200;
    dst[i][1]=yy+i*60;
    ctxRef.current.fillStyle='yellow';
    writeText({text:destinations1[i],x:xb+205,y:yy+5+i*60});
    writeText({text:destinations2[i],x:xb+205,y:yy+10+5+i*60});
    writeText({text:destinations3[i],x:xb+205,y:yy+20+5+i*60});
  }
}  
const takebutton=()=>{
  ctxRef.current.clearRect(xb+280,15,100,60);
  if(value)
    neonRect(xb+300,25,40,40,13,213,252);//cyan -TAKE BUTTON
  else
    neonRect(xb+300,25,40,40,252,90,184);
  writeText({text:"TAKE",x:xb+305,y:30});
}
const prevbutton=()=>{
  //ctxRef.current.clearRect(xb-10,15,250,610);
  neonRect(xb+300,85,40,40,13,213,252);//cyan -TAKE BUTTON
  writeText({text:"PREV",x:xb+305,y:90});
}
const drawRect = (info, style = {}) => {
  const { x, y, w, h,co } = info;
  //const { borderColor  } = style;
  ctxRef.current.beginPath();
  ctxRef.current.strokeStyle = co;
  ctxRef.current.lineWidth = 2;
  ctxRef.current.rect(x, y, w, h);
  ctxRef.current.stroke();
}
function line(x,y,l,co){
  ctxRef.current.strokeStyle=co;
  ctxRef.current.fillStyle=co;
  ctxRef.current.beginPath();
  ctxRef.current.moveTo(x,y);
  ctxRef.current.lineTo(x+l,y);//ofc3
  ctxRef.current.stroke();
}
const writeText = (info, style = {}) => {
  const { text, x, y } = info;
  const { fontSize = 10, fontFamily = 'Arial', color = 'yellow', textAlign = 'left', textBaseline = 'top' } = style;
  ctxRef.current.beginPath();
  ctxRef.current.font = fontSize + 'px ' + fontFamily;
  ctxRef.current.textAlign = textAlign;
  ctxRef.current.textBaseline = textBaseline;
  ctxRef.current.shadowBlur = 0;
  ctxRef.fillStyle = 'yellow';
  ctxRef.current.strokeStyle = 'red';  
  ctxRef.current.fillText(text, x, y);
  ctxRef.current.stroke();
};  
  return (
    <div className="Downlink">
      <canvas onMouseDown={clickEffect} ref={canvasRef} />
    </div>
  );
}