import { Breakpoint, BreakpointProvider } from 'react-socks';
import { useState } from 'react';
import "./styles.css";
import Downlink from './Downlink.js';
import Drawing from './Drawing.js';
import Datas from './Datas.js';
//import 'bootstrap/dist/css/bootstrap.css';
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";
import { FloatingButton, Item } from "react-floating-button";

var React=require('react');
var ReactDOM=require('react-dom/client');
const arr1=['UPLINK','DOWN LINK','MONITORING','DATAS'];
const myList = ["LAT:08'35'6", "LONG:76'57'55", 'Kevin', 'Dhoni', 'Alisa']; 

function Menu(){
  const [vv,setVv]=useState(0);
  const listItems = myList.map((list)=>{   
    return<li>{list}</li>;   
});
  function clickHandle(e){
    //console.log(e.target.value);
    if(e.target.value==='b1')
      setVv(1)
    else if(e.target.value==='b2')
      setVv(2)
      else
      setVv(3)
      //console.log(vv);
   };
      return (
        <BreakpointProvider>
        <div style={{backgroundColor:'black'}}>
        <ButtonGroup onClick={clickHandle}>
          <Button variant="outline-primary" value='b1'>
          UPLINK
          </Button>
          <Button variant="outline-primary" value='b2'>
          DOWN LINK
          </Button>
          <Button variant="outline-primary" value='b3'>
          Datas
          </Button>
        </ButtonGroup>
        {vv===1?<Drawing/>:null}
        {vv===2?<Downlink/>:null}
        {vv===3?<Datas/>:null}
        </div>
        </BreakpointProvider>
      );
    }
const root=ReactDOM.createRoot(document.getElementById('root'));
root.render(<Menu/>);
