import React from "react";
import { useEffect, useRef } from 'react';
import './Drawing.css';
import { Breakpoint, BreakpointProvider } from 'react-socks';
export default function Drawing() {
var sr=new Array(11);
var dst=new Array(11);
var draw=false;
var nIntervId;
var did=11;
var sid=11;
var x1=0;
var xsrc=0;
var ysrc=0;
var xmid=0;
var xdst=0;
var ydst=0;
var down=true;
var ind=0;
var ddp=0;
for (var i = 0; i < sr.length; i++) {
    sr[i] = new Array(3);
    dst[i]=new Array(3);
};
for ( i = 0; i < 11; i++) {
  for (var j = 0; j < 3; j++) {
      sr[i][j] = 11;
      dst[i][j]=11;
  }
};
const [width, setWidth] = React.useState(window.innerWidth);
const [height, setHeight] = React.useState(window.innerHeight);
const [scale, setScale] = React.useState({ x: 1, y: 1 });
const canvasRef = useRef(null);
const ctxRef = useRef(null);
const xb=30;
var value=true;
var ckt=false;
const sources1 = ['MSR1', 'MSR2', 'MSR3',' CG1',' CG2','  FS','  PD','  PD','  WM','  WM'];
const sources2 = ['  HD', '  SD ', 'FEED','  HD','  HD',' OUT','  HD','  HD','  SD','  SD'];                
const sources3 = [' OFC', ' OFC', ' OFC',' OUT',' OUT','  SD',' OUT',' OUT','OUT-1','OUT-2'];
const destinations1=[' CG1',' CG2','FS-IN',' OLD','','  WM','  WM','ENC1','ENC2'];
const destinations2=['  HD','  HD','  HD','ES','','  SD','  SD','MAIN','REDUN'];
const destinations3=['  IN','  IN','  IN','  IN','',' IN-1',' IN-2','SD-IN','SD-IN'];
const equipments=['CG1','CG2',' FS',' PD','',' WM',''];
const calculateScaleX = () => canvasRef.current.clientWidth / 600;
const calculateScaleY = () => canvasRef.current.clientHeight / 800;

const resized = () => {
  canvasRef.current.width = canvasRef.current.clientWidth;
  canvasRef.current.height = canvasRef.current.clientHeight;
  setScale({ x: calculateScaleX(), y: calculateScaleY() });
};
function canvasInit(can,scaleX,scaleY){
  const canvas = can;
  const ctx = canvas.getContext('2d');//
  ctx.scale(scaleX,scaleY);//
  ctx.lineCap = 'round';
  ctx.strokeStyle = 'yellow';//
  ctx.lineWidth = 2;//
  ctxRef.current = ctx;
}
const updateWidthAndHeight = () => {
  setWidth(window.innerWidth*2);
  setHeight(window.innerHeight*2);
};
React.useEffect(() => {
  window.addEventListener("resize", updateWidthAndHeight);
  return () => window.removeEventListener("resize", updateWidthAndHeight);
});
React.useEffect(() => {
  const currentCanvas = canvasRef.current;
  currentCanvas.addEventListener("resize", resized);
  return () => currentCanvas.removeEventListener("resize", resized);
});
function menubutton(){
  neonRect(xb-10,10,20,20,13,213,252);//cyan -TAKE BUTTON
  writeText({text:"M",x:xb-10+5,y:15});
}
function arrow(xa,ya,direction){
  if(direction===1){
  ctxRef.current.moveTo(xa,ya-3);
  ctxRef.current.lineTo(xa-7,ya);
  ctxRef.current.lineTo(xa,ya+3);
  ctxRef.current.stroke();
  }
  else{
    ctxRef.current.moveTo(xa,ya);
  ctxRef.current.lineTo(xa-7,ya-3);
  ctxRef.current.moveTo(xa-7,ya+3);
  ctxRef.current.lineTo(xa,ya);
  ctxRef.current.stroke();
  }
} 
React.useEffect(() => {
  canvasInit(canvasRef.current, scale.x, scale.y);
  ctxRef.current.beginPath();
  ctxRef.current.moveTo(0,100);
  srcbutton();
  dstbutton();
  equbutton();
  takebutton();
  prevbutton();
  menubutton();
  ctxRef.current.shadowBlur = 0;
}, [scale]);
const drawRectangle =function(x,y,w,h,border){
  const ctx=ctxRef.current;
  ctx.beginPath();
  ctx.moveTo(x+border, y);
  ctx.lineTo(x+w-border, y);
  ctx.quadraticCurveTo(x+w-border, y, x+w, y+border);
  ctx.lineTo(x+w, y+h-border);
  ctx.quadraticCurveTo(x+w, y+h-border, x+w-border, y+h);
  ctx.lineTo(x+border, y+h);
  ctx.quadraticCurveTo(x+border, y+h, x, y+h-border);
  ctx.lineTo(x, y+border);
  ctx.quadraticCurveTo(x, y+border, x+border, y);
  ctx.closePath();
  ctx.stroke();
};

var neonRect = function(x,y,w,h,r,g,b){
  const ctx=ctxRef.current;
  ctx.shadowColor = "rgb("+r+","+g+","+b+")";
  ctx.shadowBlur = 10;
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=7.5;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=6;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=4.5;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= "rgba("+r+","+g+","+b+",0.2)";
  ctx.lineWidth=3;
  drawRectangle(x,y,w,h,1.5);
  ctx.strokeStyle= '#fff';
  ctx.lineWidth=1.5;
  drawRectangle(x,y,w,h,1.5);
}
const clickEffect=({nativeEvent})=>{
  
  const {offsetX,offsetY}=nativeEvent;
  for (const [i, sx] of sr.entries()) {
    const xc=sx[0];
    const yc=sx[1];
    //SOURCE KEY
    if(offsetX>xc&&offsetX<xc+40){
      if(offsetY>yc&&offsetY<yc+40){
        srcbutton();
        neonRect(xc,yc,40,40,252,90,184);
        if(did===11){
          dstbutton();
          for(const [j,vx] of dst.entries()){
            if(vx[2]===i){
              neonRect(vx[0],vx[1],40,40,252,90,184);
            }
          }
        }
        else{
          nIntervId = setInterval(blinker, 500);
          sid=i;
        }
        equbutton();
        return;
      }
    }
  }
  //DESTINATION KEY
  for (const [i, sx] of dst.entries()) {
    const xc=sx[0];
    const yc=sx[1];
    if(offsetX>xc&&offsetX<xc+40){
      if(offsetY>yc&&offsetY<yc+40){
        dstbutton();
        srcbutton();
        equbutton();
        neonRect(xc,yc,40,40,252,90,184);
        did=i;
      return;
    }
    }
  }
  //TAKE KEY
  if(offsetX>xb+300&&offsetX<xb+340){
    if(offsetY>25&&offsetY<65){
      if(draw)
        return;
      draw=true;
      dst[did][2]=sid;
      clearInterval(nIntervId);
      value=true;
      takebutton();
      drawLink();
      nIntervId=setInterval(dl,10);
      return;
    }
  }
  //PREV button
  if(offsetX>xb+300&&offsetX<xb+340){
    if(offsetY>85&&offsetY<125){
      if(draw)
        return;
      srcbutton();
      dstbutton();
      equbutton();
      draw=true;
      value=true;
      ckt=false;
      ctxRef.current.lineCap = 'round';
      ctxRef.current.strokeStyle = 'yellow';
      ctxRef.current.lineWidth = 1;
      nIntervId=setInterval(full,8);
      return;
    }
  }
  //Menu
  if(offsetX>xb-10&&offsetX<xb+10){
    if(offsetY>10&&offsetY<30){
      ctxRef.current.clearRect(xb-10,30,300,600);
    }
  }
}

function drawLink(){
  xsrc=sr[sid][0]+45;
  ysrc=sr[sid][1]+20;
  xdst=dst[did][0]-5;
  ydst=dst[did][1]+20;
  xmid=(xdst-xsrc)/2;
  x1=0;
  if(ydst>ysrc)
    down=true;
  else
    down=false;
  ckt=true;
  ctxRef.current.moveTo(xsrc,ysrc);
}
function dl(){
  if(!ckt)
    return;
  x1=x1+1;
  if(x1>=xmid ){
    if(down){
      if(ysrc<ydst)
        ysrc=ysrc+1;
      else
        xsrc=xsrc+1;
    }
    else{
      if(ysrc>ydst)
        ysrc=ysrc-1;
      else
        xsrc=xsrc+1;
    }
  }
  else{
    xsrc=xsrc+1;
  }
  ctxRef.current.lineTo(xsrc,ysrc);
  ctxRef.current.stroke();
  if(xsrc>=xdst){
    clearInterval(nIntervId);
    did=11;
    sid=11;
    draw=false;
    ckt=false;
  }
}
function full(){
  if(!ckt){
  if(dst[ind][2]!==11){
    sid=dst[ind][2];
    did=ind;
    drawLink();
    ckt=true;
  }
}
  x1=x1+1;
  if(x1>=xmid ){
    if(down){
      if(ysrc<ydst)
        ysrc=ysrc+1;
      else
        xsrc=xsrc+1;
    }
    else{
      if(ysrc>ydst)
        ysrc=ysrc-1;
      else
        xsrc=xsrc+1;
    }
  }
  else{
    xsrc=xsrc+1;
  }
  ctxRef.current.lineTo(xsrc-4+ddp,ysrc);
  ctxRef.current.stroke();
  if(xsrc>=xdst){
    did=11;
    sid=11;
    x1=0;
    ddp=ddp+1
    ckt=false;
    if(ind<9)
      ind=ind+1;
      else{
        ind=0;
        ddp=0;
        draw=false;
      clearInterval(nIntervId)
      }
  }
}
function blinker()  {
      value=!value;
      takebutton();
};
const equbutton=()=>{
  ctxRef.current.clearRect(xb-10,0,90,600);
  neonRect(xb,532,50,20,13,213,252);//cyan-WATER MARK
  for (const [i, equipment] of equipments.entries()) {
    if(i!==4){
    neonRect(xb,190+i*60,50,40,13,213,252);//cyan
    ctxRef.current.fillStyle='yellow';
    if(i!==3){
    ctxRef.current.moveTo(xb+50,195+i*60);
    ctxRef.current.lineTo(xb+200,195+i*60);
    arrow(xb+80,195+i*60,1);
    }
    ctxRef.current.moveTo(xb+50,225+i*60);
    ctxRef.current.lineTo(xb+100,225+i*60);
    arrow(xb+80,225+i*60,0);
    ctxRef.current.stroke();
    writeText({text:equipment,x:xb+5,y:195+i*60});
  }
  }  
  //OFC TO SDI CONVERTER
  drawRect({x:xb+20,y:25,w:30,h:150,co:'cyan'});
  writeText({text:"OFC",x:xb+25,y:50});
  writeText({text:' TO',x:xb+25,y:60});
  writeText({text:'SDI',x:xb+25,y:70});
  line(xb,sr[0][1]+20,20,'yellow');
  line(xb+50,sr[0][1]+20,45,'green');
  line(xb,sr[1][1]+20,20,'yellow');
  line(xb+50,sr[1][1]+20,45,'green');
  line(xb,sr[2][1]+20,20,'yellow');
  line(xb+50,sr[2][1]+20,45,'green');
  //MODULATORS
  drawRect({x:xb+270,y:585,w:85,h:20,co:'cyan'});
  line(xb+245,dst[7][1]+20,25,'orange');
  writeText({text:"MODULATOR-M",x:xb+275,y:590});
  drawRect({x:xb+270,y:655,w:85,h:20,co:'cyan'});
  line(xb+245,dst[8][1]+20,25,'orange');
  writeText({text:"MODULATOR-R",x:xb+275,y:660});
  //IF SWITCH 1
  ctxRef.current.moveTo(xb+300,605);
  ctxRef.current.lineTo(xb+300,620);
  ctxRef.current.moveTo(xb+300,640);
  ctxRef.current.lineTo(xb+300,655);
  ctxRef.current.stroke();
  drawRect({x:xb+270,y:620,w:85,h:20,co:'cyan'});
  writeText({text:"IF SWITCH",x:xb+275,y:630});
  line(xb+355,630,25,'orange');
}  

const srcbutton=()=>{
  ctxRef.current.clearRect(xb+80,0,100,650);
  for (const [i, source] of sources1.entries()) {
    neonRect(xb+100,25+i*60,40,40,13,213,252);//cyan
    sr[i][0]=xb+100;
    sr[i][1]=25+i*60;
    ctxRef.current.fillStyle='yellow';
    writeText({text:sources1[i],x:xb+105,y:30+i*60});
    writeText({text:sources2[i],x:xb+105,y:40+i*60});
    writeText({text:sources3[i],x:xb+105,y:50+i*60});
  }
}
const dstbutton=()=>{
  const yy=160;
  ctxRef.current.clearRect(xb+160,yy-10,100,600);
  for (const [i, destination] of destinations1.entries()) {
    neonRect(xb+200,yy+i*60,40,40,13,213,252);//cyan
    dst[i][0]=xb+200;
    dst[i][1]=yy+i*60;
    ctxRef.current.fillStyle='yellow';
    writeText({text:destinations1[i],x:xb+205,y:yy+5+i*60});
    writeText({text:destinations2[i],x:xb+205,y:yy+10+5+i*60});
    writeText({text:destinations3[i],x:xb+205,y:yy+20+5+i*60});
  }
}  
const takebutton=()=>{
  ctxRef.current.clearRect(xb+280,15,100,60);
  if(value)
    neonRect(xb+300,25,40,40,13,213,252);//cyan -TAKE BUTTON
  else
    neonRect(xb+300,25,40,40,252,90,184);
  writeText({text:"TAKE",x:xb+305,y:30});
}
const prevbutton=()=>{
  //ctxRef.current.clearRect(xb-10,15,250,610);
  neonRect(xb+300,85,40,40,13,213,252);//cyan -TAKE BUTTON
  writeText({text:"PREV",x:xb+305,y:90});
}
const drawRect = (info, style = {}) => {
  const { x, y, w, h,co } = info;
  //const { borderColor  } = style;
  ctxRef.current.beginPath();
  ctxRef.current.strokeStyle = co;
  ctxRef.current.lineWidth = 2;
  ctxRef.current.rect(x, y, w, h);
  ctxRef.current.stroke();
}
function line(x,y,l,co){
  ctxRef.current.strokeStyle=co;
  ctxRef.current.fillStyle=co;
  ctxRef.current.beginPath();
  ctxRef.current.moveTo(x,y);
  ctxRef.current.lineTo(x+l,y);//ofc3
  ctxRef.current.stroke();
}
const writeText = (info, style = {}) => {
  const { text, x, y } = info;
  const { fontSize = 10, fontFamily = 'Arial', color = 'yellow', textAlign = 'left', textBaseline = 'top' } = style;
  ctxRef.current.beginPath();
  ctxRef.current.font = fontSize + 'px ' + fontFamily;
  ctxRef.current.textAlign = textAlign;
  ctxRef.current.textBaseline = textBaseline;
  ctxRef.current.shadowBlur = 0;
  ctxRef.fillStyle = 'yellow';
  ctxRef.current.strokeStyle = 'red';  
  ctxRef.current.fillText(text, x, y);
  ctxRef.current.stroke();
};  
  return (
    
    <div className="Drawing">
      <div style={{color:'yellow'}}>{`Window width = ${width}`}</div>
    <div style={{color:'yellow'}}>{`Window height = ${height}`}</div>
      <canvas onMouseDown={clickEffect} ref={canvasRef} style={{ width: "100%", height: "100%" }}/>
    </div>
    
  );
}